.Personal {
  text-align: center;
  max-width: 1150px;
  margin: auto;
  /*background-color: yellow;*/
  display: flex;
  flex-direction: column;
}

.Personal-nav {
  display: flex;
  flex-direction: row;
  margin-top: 5px;
  /*margin-left: 10px;*/
  /*margin-right: 10px;*/
}

.Personal-nav-left-btns {
  font-family: Roboto;
  font-style: normal;
  font-weight: bold;
  font-size: 20px;
  line-height: 23px;
  text-align: right;
}

.Personal-nav-left-btns a {
  text-color: 000;
  text-decoration: none;
  padding-right: 40px;
}

.Personal-nav-left-btns a:visited {
  color: #000;
}

.Personal-nav-social {
  margin-left: auto;
  margin-right: 0;
}

.Personal-nav-social button {
  margin-left: 10px;
}

